.programs {
  width: 100%;
  margin: 0;
  padding: 0;
  padding-top: 100px;
  padding-bottom: 30px;
  background-image: url("../img/programs-sketch-bg-desktop@1x.png");
  background-repeat: no-repeat;
  background-size: 680px auto;
  background-position: 49vw top;


  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/programs-sketch-bg-desktop@2x.png");
  }
}

.programs__block-container {
  display: flex;
  flex-direction: row;
}

.programs__list {
  list-style: none;
  position: relative;
  width: 228px;
  padding: 0;
  background-color: $white;
}

.programs__item {
  width: 195 px;
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;


  &:hover,
  &:focus {
  color: $yellow;
  // background: $yellow-gradient;
  // -webkit-background-clip: text;
  // -webkit-text-fill-color: transparent;
}
}

