.programs__item span {
  font-size: 15px;
  line-height: 18px;
  opacity: 0.87;
  text-align: left;
  padding: 28px 40px 24px 11px;
  margin: 0;
  cursor: pointer;
}

.programs__item--active {
  background-image: $yellow-gradient;
  color: $white;
}

.programs__item--active::after {
  content: "";
  position: absolute;
  top: 69px;
  right: -44px;
  width: 0;
  height: 0;
  border: 36px solid $white;
  border-left-color: #FFD701;
}

.programs__item--active span {
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  font-variant: small-caps;
  color: $white;
  text-shadow: $shadow-text;
}
.programs-block {
  position: relative;
}

.programs-block--hidden {
  display: none;
}

// .programs-block div {
//   position: absolute;
//   bottom: 0px;
//   left: 367px;
// }

.programs-block__title {
  @include third-level-headings();
  text-align: left;
}

.programs-block__info {
  width: 560px;
  text-align: left;
}

@media (max-width: $mobile-width) {
  .programs__block-container {
    display: flex;
    flex-direction: column;
  }

  .programs__list {
    width: 100%;
    display: flex;
    flex-direction: row;
    // transform: translateX(-50%);
    overflow: hidden;
  }

  .programs__item--active {
    border-radius: 60px;
  }

  .programs__item--active::after {
   display: none;
  }

  .programs-block__title {
    @include third-level-headings-mobile();
  }



}

